<template>
  <div id="app">
    <h1 v-if="gameOver">Game Over : {{ gameOver }}</h1>
    <h1 v-else-if="round == 1">Black's Round</h1>
    <h1 v-else-if="round == 2">White's Round</h1>
    <h1 v-else>Waiting for new player...</h1>
    <GoBoard />
  </div>
</template>

<script>
import GoBoard from "./components/GoBoard.vue";
export default {
  name: "App",
  components: {
    GoBoard
  },
  data() {
    return {
      round: 0,
      gameOver: ""
    };
  },
  created() {
    window.socket.on("round", data => {
      this.round = data;
    });
  },
  mounted() {
    window.socket.on("gameOver", data => {
      //console.log(data);
      if (data == -1 && this.gameOver == "")
        this.gameOver = "Your opponent has disconnected";
      else if (data == 1) this.gameOver = "Black Win";
      else if (data == 2) this.gameOver = "White Win";
    });
  }
};
</script>

