<template>
  <div
    class="cell"
    :class="{
      active: (isHover || status) && hoverStatus,
      black: status == 1,
      white: status == 2,
      hoverBlack: hoverStatus == 1 && status == 0,
      hoverWhite: hoverStatus == 2 && status == 0,
    }"
    @mouseover="mouseOver()"
    @mouseleave="mouseLeave()"
    @click="click()"
  />
</template>

<script>
export default {
  name: "Cell",
  props: {
    x: Number,
    y: Number,
    hoverStatus: Number
  },
  data() {
    return {
      status: 0,
      isHover: 0
    };
  },
  methods: {
    mouseOver() {
      this.isHover = 1;
    },
    mouseLeave() {
      this.isHover = 0;
    },
    click() {
      window.socket.emit("position", { x: this.x, y: this.y });
    },
    setStatus(status) {
      this.status = status;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  box-sizing: border-box;
  margin: 0;
}
.cell {
  width: 6rem;
  height: 6rem;
  display: inline-block;
  position: relative;
}
.cell:before {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  content: "";
  /* background-color: #522;*/
  background-color: #bebebe;
  width: 0.13rem;
  height: 100%;
}
.cell:after {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  content: "";
  /*background-color: #522;*/
  background-color: #bebebe;
  width: 100%;
  height: 0.13rem;
}
.cell.active:before,
.cell.active:after {
  background: none;
}
.cell.hoverBlack:hover {
  background-image: url("../assets/circle-dark.svg");
}
.cell.hoverWhite:hover {
  background-image: url("../assets/circle-light.svg");
}
.cell.black {
  background-image: url("../assets/circle-dark.svg");
}
.cell.white {
  background-image: url("../assets/circle-light.svg");
}
</style>
